// EditClient using getDerivedStateFromProps
import React, { Component, Fragment } from 'react';
// import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';

import ClientForm from './ClientForm';

import Spinner from '../layout/Spinner';

class EditClient extends Component {
  state = {
    id: '',
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    balance: ''
  };

  static getDerivedStateFromProps({ client }, state) {
    if (state.id === '' && client) {
      return client;
    } else {
      return state;
    }
  }
  // componentDidMount() {
  //   const {
  //     props: {
  //       client: { id, firstName, lastName, email, phone, balance }
  //     }
  //   } = this;

  //   // if (client && Object.keys(state).every(key => state[key] === 0)) {
  //   //   this.setState({ id, firstName, lastName, email, phone, balance });
  //   // }

  //   this.setState({ id, firstName, lastName, email, phone, balance });
  // }

  handleSubmit = e => {
    e.preventDefault();

    const {
      props: { client, firestore, history },
      state
    } = this;

    const updatedClient = { ...state };

    if (updatedClient.balance === '' || isNaN(updatedClient.balance)) {
      updatedClient.balance = '0';
    }

    firestore
      .update({ collection: 'clients', doc: client.id }, updatedClient)
      .then(history.push({ pathname: `/client/${client.id}` }));
  };

  handleInputChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    const {
      props: {
        client,
        settings: { disableBalanceOnEdit }
      },
      state
    } = this;

    if (client) {
      return (
        <Fragment>
          <ClientForm
            formTitle="Edit Client"
            handleSubmit={this.handleSubmit}
            handleInputChange={this.handleInputChange}
            client={state}
            isBalanceDisabled={disableBalanceOnEdit}
          />
          {/* <div className="row">
            <div className="col-md-6">
              <Link to={`/client/${client.id}`} className="btn btn-link">
                <i className="fas fa-arrow-circle-left" />
                Back To Dashboard
              </Link>
            </div>
          </div> */}

          {/* <div className="card">
            <div className="card-header">Edit Client</div>
            <div className="card-body">
              <form onSubmit={this.handleSubmit}>
                <div className="form-group">
                  <label htmlFor="firstName">First Name</label>
                  <input
                    type="text"
                    name="firstName"
                    className="form-control"
                    minLength="2"
                    required
                    onChange={this.handleInputChange}
                    value={firstName}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="lastName">Last Name</label>
                  <input
                    type="text"
                    name="lastName"
                    className="form-control"
                    minLength="2"
                    required
                    onChange={this.handleInputChange}
                    value={lastName}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    name="email"
                    className="form-control"
                    onChange={this.handleInputChange}
                    value={email}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="phone">Phone</label>
                  <input
                    type="text"
                    name="phone"
                    className="form-control"
                    minLength="10"
                    required
                    onChange={this.handleInputChange}
                    value={phone}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="balance">Balance</label>
                  <input
                    type="number"
                    name="balance"
                    className="form-control"
                    onChange={this.handleInputChange}
                    value={balance}
                    disabled={disableBalanceOnEdit}
                  />
                </div>

                <input
                  type="submit"
                  value="Submit"
                  className="btn btn-primary btn-block"
                />
              </form>
            </div>
          </div> */}
        </Fragment>
      );
    } else {
      return <Spinner />;
    }
  }
}

EditClient.propTypes = {
  firestore: PropTypes.object.isRequired
};

export default compose(
  firestoreConnect(props => [
    { collection: 'clients', storeAs: 'client', doc: props.match.params.id }
  ]),
  connect(({ firestore: { ordered }, settings }, props) => ({
    client: ordered.client && ordered.client[0],
    settings
  }))
)(EditClient);
