import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';

import BackToDashboardButton from '../layout/BackToDashboardButton';
import Spinner from '../layout/Spinner';

class ClientDetails extends Component {
  state = {
    showBalanceUpdate: false,
    balanceUpdateAmount: ''
  };

  handleBalanceSubmit = e => {
    e.preventDefault();

    const {
      props: { client, firestore },
      state: { showBalanceUpdate, balanceUpdateAmount }
    } = this;

    const clientUpdate = {
      balance: isNaN(parseFloat(balanceUpdateAmount))
        ? 0
        : parseFloat(balanceUpdateAmount)
    };

    firestore.update({ collection: 'clients', doc: client.id }, clientUpdate);

    this.setState({
      showBalanceUpdate: !showBalanceUpdate,
      balanceUpdateAmount: ''
    });
  };

  handleDelete = () => {
    const {
      props: { client, firestore, history }
    } = this;

    firestore
      .delete({ collection: 'clients', doc: client.id })
      .then(() => history.push('/'));
  };

  handleChange = e => this.setState({ [e.target.name]: e.target.value });

  render() {
    const {
      props: { client },
      state: { showBalanceUpdate, balanceUpdateAmount }
    } = this;

    let balanceForm = '';

    if (showBalanceUpdate) {
      balanceForm = (
        <form onSubmit={this.handleBalanceSubmit}>
          <div className="input-group">
            <input
              type="number"
              className="form-control"
              name="balanceUpdateAmount"
              placeholder="Add New Balance"
              value={balanceUpdateAmount}
              onChange={this.handleChange}
            />
            <div className="input-group-append">
              <input
                type="submit"
                value="Update"
                className="btn btn-outline btn-dark"
              />
            </div>
          </div>
        </form>
      );
    } else {
      balanceForm = null;
    }

    if (client) {
      return (
        <Fragment>
          {/* <div className="row">
            <div className="col-md-6">
              <Link to="/" className="btn btn-link">
                <i className="fas fa-arrow-circle-left" /> Back To Dashboard
              </Link>
            </div>

            <div className="col-md-6">
              <div className="btn-group float-right">
                <Link to={`/client/edit/${client.id}`} className="btn btn-dark">
                  Edit
                </Link>
                <button className="btn btn-danger" onClick={this.handleDelete}>
                  Delete
                </button>
              </div>
            </div>
          </div> */}
          <BackToDashboardButton>
            <div className="col-md-6">
              <div className="btn-group float-right">
                <Link to={`/client/edit/${client.id}`} className="btn btn-dark">
                  Edit
                </Link>
                <button className="btn btn-danger" onClick={this.handleDelete}>
                  Delete
                </button>
              </div>
            </div>
          </BackToDashboardButton>
          <hr />

          <div className="card">
            <h3 className="card-header">
              {client.firstName} {client.lastName}
            </h3>
            <div className="card-body">
              <div className="row">
                <div className="col-md-8 col-sm-6">
                  <h4>
                    Client ID:{' '}
                    <span className="text-secondary">{client.id}</span>
                  </h4>
                </div>

                <div className="col-md-4 col-sm-6">
                  <h3 className="pull-right">
                    Balance:{' '}
                    <span
                      className={
                        parseFloat(client.balance) > 0
                          ? 'text-danger'
                          : 'text-success'
                      }>
                      ${parseFloat(client.balance).toFixed(2)}
                    </span>
                    <small>
                      <a
                        href="#!"
                        onClick={() =>
                          this.setState({
                            showBalanceUpdate: !showBalanceUpdate
                          })
                        }>
                        {' '}
                        <i className="fas fa-pencil-alt" />
                      </a>
                    </small>
                  </h3>
                  {balanceForm}
                </div>
              </div>

              <hr />
              <ul className="list-group">
                <li className="list-group-item">
                  Contact Email: {client.email}
                </li>
                <li className="list-group-item">
                  Contact Number: {client.phone}
                </li>
              </ul>
            </div>
          </div>
        </Fragment>
      );
    } else {
      return <Spinner />;
    }
  }
}

ClientDetails.propTypes = {
  firestore: PropTypes.object.isRequired
};

export default compose(
  firestoreConnect(props => [
    { collection: 'clients', storeAs: 'client', doc: props.match.params.id }
  ]),
  connect(({ firestore: { ordered } }, props) => ({
    client: ordered.client && ordered.client[0]
  }))
)(ClientDetails);
