import React from 'react';
import PropTypes from 'prop-types';

const alert = ({ message, messageType }) => {
  return (
    <div
      // className={`alert ${
      //   messageType === 'success'
      //     ? 'alert-success'
      //     : messageType === 'error'
      //     ? 'alert-danger'
      //     : null
      // }`}>

      className={
        messageType === 'success'
          ? 'alert alert-success'
          : messageType === 'error'
          ? 'alert alert-danger'
          : null
      }>
      {message}
    </div>
  );
};

alert.propTypes = {
  message: PropTypes.string.isRequired,
  messageType: PropTypes.string.isRequired
};

export default alert;
