import React from 'react';
// import { Link } from 'react-router-dom';
import Proptypes from 'prop-types';
import { connect } from 'react-redux';

import {
  setDisableBalanceOnAdd,
  setDisableBalanceOnEdit,
  setAllowRegistration
} from '../../actions/settingsActions';

import BackToDashboardButton from '../layout/BackToDashboardButton';

const settings = ({
  setDisableBalanceOnAdd,
  setDisableBalanceOnEdit,
  setAllowRegistration,
  settings: { disableBalanceOnAdd, disableBalanceOnEdit, allowRegistration }
}) => {
  return (
    <div>
      {/* <div className="row">
        <div className="col-md-6">
          <Link to="/" className="btn btn-link">
            <i className="fas fa-arrow-circle-left" /> Back To Dashboard
          </Link>
        </div>
      </div> */}
      <BackToDashboardButton />

      <div className="card">
        <div className="card card-header">Edit Settings</div>
        <div className="card-body">
          <form>
            <div className="form-group">
              <label>Allow Registration</label>{' '}
              <input
                type="checkbox"
                name="allowRegistration"
                checked={allowRegistration}
                onChange={setAllowRegistration}
              />
            </div>
            <div className="form-group">
              <label>Disable Balance On Add</label>{' '}
              <input
                type="checkbox"
                name="disableBalanceOnAdd"
                checked={disableBalanceOnAdd}
                onChange={setDisableBalanceOnAdd}
              />
            </div>
            <div className="form-group">
              <label>Disable Balance On Edit</label>{' '}
              <input
                type="checkbox"
                name="disableBalanceOnEdit"
                checked={disableBalanceOnEdit}
                onChange={setDisableBalanceOnEdit}
              />
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

settings.propTypes = {
  settings: Proptypes.object.isRequired,
  setDisableBalanceOnAdd: Proptypes.func.isRequired,
  setDisableBalanceOnEdit: Proptypes.func.isRequired,
  setAllowRegistration: Proptypes.func.isRequired
};

export default connect(
  (state, props) => ({
    auth: state.firebase.auth,
    settings: state.settings
  }),
  { setDisableBalanceOnAdd, setDisableBalanceOnEdit, setAllowRegistration }
)(settings);
